html,
body {
  padding: 0;
  margin: 0;
  height: 100%;
  overflow: hidden;
  -webkit-overflow-scrolling: touch;
}

body {
  font-family: "Barlow Condensed Light";
  line-height: 1.6rem;
  background-color: #000000;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

#APP,
#Fuser,
#Page {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
}

#Menu {
  z-index: 1;
}

#Fuser {
  opacity: 0;
  transition: opacity 0.6s;
  z-index: 0;
}
#Fuser.visible {
  opacity: 1;
}

#Menu {
  position: absolute;
  top: 0;
  right: 0;
}
#Menu .Launcher {
  background-color: #cccccc;
  padding: 16px;
  cursor: pointer;
  transition: padding 0.3s ease-out 0.3s;
}
#Menu .Options {
  position: absolute;
  top: 0;
  right: 64px;
  background-color: #414141;
  padding: 16px 0;
  transform: translateY(-100%);
  transition-property: transform;
  transition: transform 0.3s ease-out;
}
#Menu .Options .Option {
  background-color: #414141;
  padding: 16px 24px;
  color: #ffffff;
  cursor: pointer;
  font-family: "Barlow Condensed SemiBold";
  font-size: 1.4rem;
}
#Menu .Options .Option:hover {
  background-color: #b63913;
}
#Menu:hover .Launcher {
  padding: 20px;
  transition: padding 0.3s ease-out;
}
#Menu:hover .Options {
  transform: translateY(0);
  transition: transform 0.3s ease-out 0.3s;
}

#Content {
  background-color: #8f8f8f;
}

.Page.Wordpress,
.Page.React {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.Page.Wordpress .Text,
.Page.React .Text {
  font-family: "Montserrat ExtraBold";
  font-size: 50vw;
  line-height: 1em;
  white-space: nowrap;
  position: absolute;
  top: 20%;
  left: 0;
  color: #4dbfd9;
  transform: translateX(0);
  transition: transform 1s ease-in;
}
.Page.Wordpress .Description,
.Page.React .Description {
  position: relative;
  width: 90%;
  max-width: 400px;
  top: 20%;
  padding: 24px;
  background-color: rgba(0, 0, 0, 0.5);
  box-sizing: border-box;
  border-radius: 4px;
  color: #f7f7f7;
}
.Page.Wordpress:hover .Text,
.Page.React:hover .Text {
  transform: translateX(-100%);
}

@keyframes home-text-fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
#Page.Home .Clouds {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #326696;
  background: linear-gradient(356deg, #000763 0%, #326696 100%);
}
#Page.Home .Clouds iframe {
  width: 100%;
  height: 100%;
  border: none;
  position: relative;
  z-index: 0;
}
#Page.Home .Text {
  font-family: "Barlow Condensed ExtraBold";
  font-size: clamp(120px, 30.1vw, 200px);
  line-height: 0.7em;
  white-space: nowrap;
  position: absolute;
  bottom: 5px;
  left: -4px;
  color: #4dbfd9;
  z-index: 1;
  mix-blend-mode: luminosity;
  letter-spacing: -0.07em;
  opacity: 0;
  animation-name: home-text-fade-in;
  animation-duration: 2s;
  animation-delay: 1s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}
#Page.Home .Text.visible {
  opacity: 1;
}

#Page.Gamification .Rain {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
#Page.Gamification .Rain .videobg {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
#Page.Gamification .Rain canvas#container {
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes react-text-fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
#Page.React .Text {
  position: relative;
  font-family: "Barlow Condensed ExtraBold";
  font-size: clamp(120px, 30.1vw, 200px);
  line-height: 0.7em;
  white-space: nowrap;
  position: absolute;
  bottom: 30%;
  left: -4px;
  color: #c50000;
  letter-spacing: -0.07em;
  opacity: 0;
  z-index: 0;
  animation-name: react-text-fade-in;
  animation-duration: 2s;
  animation-delay: 1s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}
#Page.React .Fire {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  mix-blend-mode: color-burn;
  z-index: 1;
}
#Page.React .Fire canvas#view {
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes react-text-fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
#Page.Wordpress .Text {
  position: relative;
  font-family: "Barlow Condensed ExtraBold";
  font-size: clamp(120px, 30.1vw, 200px);
  line-height: 0.7em;
  white-space: nowrap;
  position: absolute;
  bottom: 30%;
  left: -4px;
  color: #ffffff;
  letter-spacing: -0.07em;
  opacity: 0;
  z-index: 0;
  animation-name: react-text-fade-in;
  animation-duration: 2s;
  animation-delay: 1s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}
#Page.Wordpress .Blur {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  filter: sepia(100%) saturate(12) hue-rotate(233deg) brightness(2);
  mix-blend-mode: difference;
}
#Page.Wordpress .Blur #container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}